.Modal{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 30%;
    background-color: #ffc37a;
    border: 1px solid chocolate;
    position: fixed;
    top: 30vh;
    right: 30%;
    padding: 3% 5%;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}




